export default {
	data() {
		return {
			refreshing: false,
			itemList: [],
			list: [],
			searchText:null,
			fetchPageNum: 1
		}
	},
	mounted(){  //template挂载到页面时调用
	    this.getData(); 
	},
	methods: {
		getData() {
			uni.request({
				url: this.$serverUrl + '/getNews',
				success: (ret) => {
					console.log('data', ret);
					if (ret.statusCode !== 200) {
						console.log('失败!');
					} else {
						this.list = ret.data.value;
						console.log('list', this.list);
					}
				}
			});
			console.log('list', this.list);
		},
		getSearchContent(){
			uni.request({
				url: this.$serverUrl + '/getNews', //仅为示例，并非真实接口地址。
				data: {//参数
					"query":this.searchText,
					"mkt":"zh-CN",
					"count":"10"
					},
			    method:'POST',
				success: res => {
					console.log('返回', res.data);
					this.list = res.data.value;
				}
			});
		},
		See (e) {
		        window.location.href = e;
		},
		onInput(e) {
		    console.log(e.detail);
			var string = e.detail;
			let reg=new RegExp(' ','g')//g代表全部
		    this.searchText = JSON.stringify(string).replace(reg,'+');
			this.getSearchContent();
			console.log("replace",this.searchText);
		}
	}
}
